
.world-vision-page {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.custom-items-display {
  background: rgba(10, 20, 40, 0.85);
  border: 1px solid rgba(180, 160, 120, 0.4);
  backdrop-filter: blur(8px);
  padding: 2rem;
  border-radius: 12px;
  color: #e0e0e0;
  max-height: 85%;
  width: 70%;
  overflow-y: auto;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.5);

  h3 {
    color: #f5e8c8;
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  h4 {
    color: #d0b888;
    font-size: 1.2rem;
    margin-top: 1.5rem;
    margin-bottom: 0.8rem;
    border-bottom: 1px solid rgba(180, 160, 120, 0.2);
    padding-bottom: 0.5rem;
  }

  ul {
    list-style: none;
    padding-left: 0;
  }

  li {
    margin-bottom: 0.7rem;
    line-height: 1.6;
    strong {
      color: #c8aa7a;
      cursor: pointer;
    }
    span {
      cursor: pointer;
    }
  }

  button {
    display: block;
    margin: 2rem auto 0;
    padding: 0.8rem 2rem;
    background: rgba(180, 160, 120, 0.2);
    color: #f5e8c8;
    border: 1px solid rgba(180, 160, 120, 0.5);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    &:hover {
      background: rgba(180, 160, 120, 0.4);
      box-shadow: 0 0 15px rgba(245, 232, 200, 0.3);
    }
  }
}

.category-section {
  margin-bottom: 1rem;
}

.reset-button {
  position: absolute;
  bottom: 2rem;
  left: 3rem;
  padding: 0.8rem 1.5rem;
  background: transparent;
  color: #a0998f;
  border: 1px solid #5a5245;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background: rgba(199, 67, 67, 0.1);
    color: #e0dcd1;
    border-color: #c74343;
  }
}
