
.custom-skills-creator {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
  padding-left: 16px;
  border-left: 2px solid rgba(255, 255, 255, 0.1);
}

.custom-skill-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 4px 8px;
  border-radius: 4px;
}

.custom-input-group {
  align-items: flex-start;
}

.custom-input-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto auto;
  gap: 8px 16px;
  align-items: end;
  grid-template-areas:
    'artName skillName'
    'artDesc skillDesc'
    'artSkills skillButton'
    'addArtButton addArtButton';
}

.custom-input-group > input[placeholder*='技艺名称'] {
  grid-area: artName;
}
.custom-input-group > textarea[placeholder*='技艺详情'] {
  grid-area: artDesc;
}
.custom-input-group > .custom-skills-creator {
  grid-area: artSkills;
}
.custom-input-group > button[type='button'] {
  grid-area: addArtButton;
}

.skill-input-row {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
  gap: 8px;
  grid-template-areas:
    'skillNameInput'
    'skillDescInput'
    'skillAddButton';
}
.skill-input-row input {
  grid-area: skillNameInput;
}
.skill-input-row textarea {
  grid-area: skillDescInput;
}
.skill-input-row button {
  grid-area: skillAddButton;
}

.selected-item-detailed {
  background-color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  padding: 8px 12px;
  margin-top: 8px;
}

.item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}

.item-desc {
  font-size: 0.9em;
  color: #a0998f;
  margin-top: 4px;
  white-space: pre-wrap;
}

.item-skills {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 0.85em;
}

.item-skills strong {
  color: #c8aa7a;
}

.item-skills ul {
  list-style-type: none;
  padding-left: 10px;
  margin-top: 4px;
}
