
.world-vision-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  width: 80%;
  max-width: 800px;
  margin: auto;
}

.title {
  font-size: 2rem;
  color: #e0dcd1;
  margin-bottom: 1rem;
}

.description {
  font-size: 1rem;
  color: #a0998f;
  margin-bottom: 2rem;
  text-align: center;
  line-height: 1.6;
}

.vision-textarea {
  width: 100%;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid #5a5245;
  border-radius: 4px;
  color: #e0dcd1;
  font-size: 1rem;
  padding: 1rem;
  resize: vertical;
  margin-bottom: 2rem;

  &::placeholder {
    color: #7a7265;
  }
}

.submit-button {
  padding: 0.8rem 2rem;
  font-size: 1.2rem;
  color: #e0dcd1;
  background-color: #5a5245;
  border: 1px solid #8b7355;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: #6b6255;
  }

  &:disabled {
    background-color: #3a3225;
    cursor: not-allowed;
  }
}
